import React, {Component} from 'react';
import {T} from './Utils'

class Boards extends Component {
    handleClick = (e) => {
        console.log('---', e.target.getAttribute('data-key'))
        window.location.href = '/boards/' + e.target.getAttribute('data-key')
    }

    renderBoards() {
        if (!this.props.boards) return;

        return this.props.boards.map(brd => {
            let style = {
                backgroundImage: `url(${'/static/images/' + brd.id + '.png'})`
            }

            return (
                <div className="board" style={style} onClick={this.handleClick} data-key={brd.id}>
                    <div data-key={brd.id}>
                        <h3 data-key={brd.id}>{brd.name}</h3>
                    </div>
                </div>
            )
        })
    }

    render() {
        return (
            <div>
                <div>
                    <ul className="p-breadcrumbs">
                        <li className="p-breadcrumbs__item"><a href="/">{T('home')}</a></li>
                        <li className="p-breadcrumbs__item">{T('boards')}</li>
                    </ul>
                </div>
                <h2>{T('choose-board')}</h2>
                <p>{T('choose-board-desc')}</p>

                {this.renderBoards()}
            </div>
        );
    }
}

export default Boards;